// 此文件用于加载公共方法
module.exports = {
  nullClick () {
    this.$alert('本系统为教学系统，目前只提供常用操作功能', '信息', {
      confirmButtonText: '确定'
    })
  },
  resetForm (formName) { // 重置表格
    this.$refs[formName].resetFields()
  },
  selsChange (sels) { // 标记选择方法
    this.sels = sels
  },
  deleteClick (formId) { // 删除的操作
    if (this.sels.length === 0) {
      this.$message.warning('选中才能删除')
      return
    }
    this.$confirm(`是否要删除选中的${this.sels.length}条数据`, '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(() => {
      let sels = this.sels
      let deleteData = {} // 删除的data
      deleteData.formId = formId
      deleteData.infoId = []
      deleteData.serviceId = this.$store.state.service.servicesId
      for (let i in sels) {
        deleteData.infoId.push(sels[i].infoId)
      }
      this.$store.commit('deleteFormInfo', deleteData)
      this.sels.length = 0
      let tableRef = `${formId}Table`
      this.$refs[tableRef].clearSelection() // 清除所有的选择
      this.isSelectNow = false
      this.$message({
        type: 'success',
        message: '删除成功!'
      })
    }).catch(() => {
      this.$message({
        type: 'info',
        message: '已取消删除'
      })
    })
  },
  submittedClick (formId) { // 点击报送按钮
    if (this.sels.length === 0) {
      this.$message.warning('选中才能报送')
      return
    }
    this.$confirm(`是否要报送选中的${this.sels.length}条数据`, '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(() => {
      let submittedData = {}
      submittedData.formId = formId
      submittedData.infoId = []
      submittedData.serviceId = this.$store.state.service.servicesId
      for (let i in this.sels) {
        submittedData.infoId.push(this.sels[i].infoId)
      }
      this.$store.commit('updataBaosong', submittedData)
      this.$message({
        type: 'success',
        message: '报送成功!'
      })
    }).catch((e) => {
      console.log(e)
      // this.$message({
      //   type: 'info',
      //   message: '已取消报送'
      // })
    })
  },
  fankuiClick (formId) { // 点击报送按钮
    if (this.sels.length === 0) {
      this.$message.warning('选中才能获取反馈')
      return
    }
    this.$confirm(`是否要获取选中的${this.sels.length}条数据的反馈(只会反馈报送中的数据)`, '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(() => {
      let submittedData = {}
      submittedData.formId = formId
      submittedData.infoId = []
      submittedData.serviceId = this.$store.state.service.servicesId
      for (let i in this.sels) {
        submittedData.infoId.push(this.sels[i].infoId)
      }
      this.$store.commit('updataFankui', submittedData)
      this.$message({
        type: 'success',
        message: '获取反馈成功!'
      })
    }).catch((e) => {
      this.$message({
        type: 'info',
        message: '已取消反馈'
      })
    })
  },
  selectObjCompare (selectData, allData) { // 数组比较方法
    for (let i in selectData) {
      if (selectData[i] === '') continue
      if (!allData[i] && allData[i] !== '') continue
      if (selectData[i] !== allData[i]) return false
    }
    return true
  },
  selectClick (formId, selectData) { // 点击查询按钮
    var _this = this
    _this.selectArr = []
    this.isSelectNow = true
    let allInfo = _this.$store.getters.getFormContent(formId)
    for (let i in allInfo) {
      if (_this.selectObjCompare(selectData, allInfo[i])) {
        _this.selectArr.push(allInfo[i])
      }
    }
    _this.$forceUpdate()
  },
  addFormClick () {
    this.centerDialogVisible = true
  },
  goBack () {
    history.back(-1)
  },
  updataClick (formId) { // 选中对号批量修改的
    if (this.sels.length !== 1) {
      this.$message.warning('只能选中一个的时候修改')
      return
    }
    this.centerDialogVisible = true
    // this[formId] = this.$store.getters.getFormOneInfo(formId, this.sels[0].infoId)
    this.$data[formId] = JSON.parse(JSON.stringify(this.sels[0]))
    this.isUpdata = true
  },
  changeSelectClick () { // 点击修改的按钮控制状态
    if (this.isSelect) {
      this.isSelect = false
    } else {
      this.isSelect = true
    }
  },
  addMetaInfo (tableName) { // 用于提交主线的配偶的信息
    let meta = {}
    let nowTable = this[tableName]
    meta.personInfoId = nowTable.personInfoId
    meta.isMate = nowTable.isMate
    meta.mateCardType = nowTable.mateCardType
    meta.mateCardNumber = nowTable.mateCardNumber
    meta.mateName = nowTable.mateName
    this.$store.commit('addMeta', meta)
  },
  personChange (tableName) {
    let storeMate = this.$store.getters.getMate(this[tableName].personInfoId)
    if (storeMate) {
      this[tableName].isMate = storeMate.isMate
      this[tableName].mateCardType = storeMate.mateCardType
      this[tableName].mateCardNumber = storeMate.mateCardNumber
      this[tableName].mateName = storeMate.mateName
    }
  }
}



