<template>
  <div style="border:1px rgb(224, 224, 224) solid;margin:10px;padding-top:15px">
    <el-form ref="form" :model="selectCollection" label-width="80px">
      <div style="display:flex">
        <el-form-item label="工号">
          <el-input v-model="selectCollection.employmentNumber"></el-input>
        </el-form-item>
        <el-form-item label="姓名">
          <el-input v-model="selectCollection.personName"></el-input>
        </el-form-item>
        <el-form-item label="证件号码">
          <el-input v-model="selectCollection.cardNumber"></el-input>
        </el-form-item>
        <el-form-item label="是否残孤烈">
          <el-select v-model="selectCollection.isCangulie">
            <el-option label="是" :value="true"></el-option>
            <el-option label="否" :value="false"></el-option>
          </el-select>
        </el-form-item>
      </div>

      <el-button
        size="small"
        type="primary"
        @click="selectClick(formId)"
        style="  position: relative ;left:90%"
      >查询
      </el-button>
      <el-button size="small" style="position: relative ;left:90%;">重置</el-button>

    </el-form>
  </div>
</template>
<script>
export default {
  components: {},
  data () {
    return {
      selectCollection: {
        texPlayerNumber: '',
        employmentNumber: '',
        personName: '',
        cardNumber: '',
        isYanzheng: '',
        isBaosong: '',
        porsonStatus: '',
        isCangulie: '',
        employmentType: ''
      },
      selectStatus: []
    }
  },
  props: {
    formId: { type: String }
  },
  created () {
  },
  methods: {
    nullClick () {
      this.$emit('nullClick')
    },
    selectClick (formId) {
      this.$emit('selectClick', formId, this.selectCollection)
    }
  }
}
</script>
<style scoped>
  .el-form-item {
    margin-bottom: 5px;
  }
</style>



