<template>
  <div style="border:1px rgb(224, 224, 224) solid;margin:10px;padding-top:15px;position:relative">
    <el-form ref="form" :model="selectCollection" label-width="80px" style="position:relative">
      <div style="display:flex">
        <el-form-item label="工号">
          <el-input v-model="selectCollection.employmentNumber"></el-input>
        </el-form-item>
        <el-form-item label="姓名">
          <el-input v-model="selectCollection.personName"></el-input>
        </el-form-item>
        <el-form-item label="证件号码">
          <el-input v-model="selectCollection.cardNumber"></el-input>
        </el-form-item>
      </div>
      <div class="flex">
        <el-form-item label="报送状态">
          <el-checkbox-group v-model="selectStatus">
            <el-checkbox label="待报送" name="type"></el-checkbox>
            <el-checkbox label="待反馈" name="type"></el-checkbox>
            <el-checkbox label="报送成功" name="type"></el-checkbox>
            <el-checkbox label="报送失败" name="type"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item style="display:inline-block">
          <el-button size="small" @click="handleCheckAllChange">全选</el-button>
          <el-button size="small" @click="handCancle">全不选</el-button>
        </el-form-item>
      </div>

      <el-button
        size="small"
        type="primary"
        @click="selectClick(formId)"
        style="  position: absolute ;right:80px"
      >查询
      </el-button>
      <el-button size="small" style="position: absolute ;right:10px;">重置</el-button>
      <div style="position: relative ;width:10px; height:45px"></div>
    </el-form>
  </div>
</template>
<script>
export default {
  components: {},
  data () {
    return {
      selectCollection: {
        texPlayerNumber: '',
        employmentNumber: '',
        personName: '',
        cardNumber: '',
        isYanzheng: '',
        isBaosong: '',
        porsonStatus: '',
        isCangulie: '',
        employmentType: ''
      },
      selectStatus: []
    }
  },
  props: {
    formId: { type: String }
  },
  created () {
  },
  methods: {
    nullClick () {
      this.$emit('nullClick')
    },
    handleCheckAllChange () {
      this.selectStatus = ['待报送', '待反馈', '报送成功', '报送失败']
    },
    handCancle () {
      this.selectStatus = []
    },
    selectClick (formId) {
      this.$emit('selectClick', formId, this.selectCollection)
    }
  }
}
</script>
<style scoped>
  .el-form-item {
    margin-bottom: 5px;
  }
</style>



