<template>
  <div>
    <div class="hot-notice">
      <span style="font-size: 18px;font-weight: bold;line-height: 18px">扣除年度：{{company[0].shenbao_month}}</span>
      <img src="../../../../static/img/message.png" alt style="width: 18px;height: 18px;margin-bottom: 4px"/>
      <span>在任一采集页面进行【导入】、【下载更新】或是选择【导出全部】，均是对所有采集项目的操作，无需重复操作</span>
    </div>
    <div class="rg-top" style="height:50px">
      <div class="rg-top-bottom">
        <div style="line-height:45px;">
          <el-button type="primary" size="small" @click="addFormClick">新增</el-button>
          <el-dropdown>
            <el-button type="primary" size="small" :split-button="true">
              导入
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native=" nullClick">导入文件</el-dropdown-item>
              <el-dropdown-item @click.native=" nullClick">模板下载</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <el-button type="primary" size="small" @click="deleteClick(formId)">删除</el-button>
          <el-button type="primary" size="small" @click="submittedClick(formId)">报送</el-button>
          <el-button type="primary" size="small" @click="fankuiClick(formId)">获取反馈</el-button>
          <el-dropdown>
            <el-button type="primary" size="small" :split-button="true">
              下载更新
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native=" nullClick">全部人员</el-dropdown-item>
              <el-dropdown-item @click.native=" nullClick">指定人员</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <el-dropdown>
            <el-button type="primary" size="small" :split-button="true">
              导出
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native=" nullClick">选中人员</el-dropdown-item>
              <el-dropdown-item @click.native=" nullClick">全部人员</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'

export default {
  props: {
    formId: { type: String }
  },
  computed: {
    ...mapGetters(['company'])
  },
  methods: {
    deleteClick (formId) {
      this.$emit('deleteClick', formId)
    },
    addFormClick () {
      this.$emit('addFormClick')
    },
    nullClick () {
      this.$emit('nullClick')
    },
    submittedClick (formId) {
      this.$emit('submittedClick', formId)
    },
    fankuiClick (formId) {
      this.$emit('fankuiClick', formId)
    }
  }
}
</script>
<style scoped>
  .hot-notice {
    border: solid 1px #d2e7fb;
    background-color: #eaf4ff;
    box-sizing: border-box;
    margin: 10px 10px 0 10px;
    padding: 8px;
    line-height: 20px;
    text-align: left;
  }

  .rg-top {
    border: solid 1px #e0e0e0;
    padding: 0 10px;
    margin: 0 10px 0 10px;
    font-size: 16px;
    font-weight: bold;
    color: #000000;
  }

  .rg-top-top {
    height: 40px;
    overflow: hidden;
    padding: 0 10px;
    line-height: 40px;
    box-sizing: border-box;
  }
</style>



